.template-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(34, 34, 34, 0.95);
    box-shadow: 0 2px 5px rgba(0,0,0,0.3);
    z-index: 999;
    transition: all 0.3s ease;
}

.template-header-inner {
    max-width: 1200px;
    margin: 0 auto;
    padding: 15px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.template-header .logo {
    font-size: 1.8rem;
    font-weight: bold;
}

.template-header .logo img {
    height: 40px;
    transition: transform 0.3s ease;
}

.template-header .logo img:hover {
    transform: scale(1.05);
}

.template-header nav ul {
    display: flex;
    list-style: none;
    gap: 20px;
    margin: 0;
    padding: 0;
}

.template-header nav a {
    color: #C3E88D;
    text-decoration: none;
    font-weight: 600;
    font-size: 0.95rem;
    transition: all 0.3s ease;
    padding: 5px 10px;
    border-radius: 4px;
}

.template-header nav a:hover {
    color: #fff;
    background-color: rgba(195, 232, 141, 0.1);
}

/* 모바일 반응형 */
@media (max-width: 768px) {
    .template-header-inner {
        padding: 10px 20px;
    }

    .template-header nav ul {
        gap: 10px;
    }

    .template-header nav a {
        font-size: 0.85rem;
        padding: 4px 8px;
    }
} 